body {
  margin: 0;
  padding: 0;
  background-color: #313131;
  font-family: "Lato", "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.title {
  font-size: 6vw;
  font-weight: 900;
}

.subtitle {
  font-size: 4vw;
  font-weight: 400;
}
header {
    position: relative;
    z-index: 2;
    background-color: #eddd09;
    color: white;
    padding: 5vw;
    margin: 0 5vw 5vw 5vw;
    box-shadow: 0 2px 30px rgba(0,0,0,0.28);
}

header .title {
    margin: 0 0 2vw 0;
}

header .subtitle, .teacher {
    margin: 2vw 0 0;
    font-size: 4vw;
}

@media only screen and (min-width: 800px) {
    header {
        margin: 5vw 0 0 5vw;
        width: 50vw;
        padding: 3vw 5vw;
    }
    header .title {
        font-size: 2.5vw;
        margin: 0 0 1vw 0;
    }
    header .subtitle,.teacher {
        font-size: 1.5vw;
        font-weight: 300;
        margin: 0.5vw 0 0;
    }
}
.scoreboard-container {
    background-color: #eddd09;
    box-shadow: 0 2px 30px 0 rgba(0,0,0,0.28);
    margin: -10vw 0 15vw 10vw;
    position: relative;
    z-index: 1;
    padding: 10vw 5vw 5vw;
}
.scoreboard-container .scoreboard-title {
    font-size: 5vw;
    font-weight: 600;
    margin: 5vw 0 0;
}

.scoreboard {
    margin: 5vw 0 0;
}

.scoreboard-tab {
    margin: 2vw 0 0;
    display: flex;
    width: 100%;
}
.scoreboard-tab .tab-item {
    padding: 1vw 0;
    background-color: #fcfbf4;
    width: 25%;
    text-align: center;
    cursor: pointer;
    transition: all 0.2s ease;
}
.scoreboard-tab .tab-item.selected {
    background-color: #eddd09;
    color: white;
}
.scoreboard-tab .tab-item:hover {
    background-color: #fff7a0;
}

.scoreboard .scoreboard-header, .scoreboard-item {
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr 3fr 1fr 1fr;
    font-size: 4vw;
    cursor: pointer;
}
.scoreboard .scoreboard-header {
    margin: 0 0 4vw 0;
    font-weight: 600;
    border-bottom: 1px solid #333;
    padding: 0 0 2vw 0;
}

.scoreboard-item {
    padding: 2vw;
    margin: 1vw -2vw;
}

.options-container {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 60px;
    background-color: #eddd09;
    box-shadow: 0 2px 30px 0 rgba(0,0,0,0.28);
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2vw;
    align-items: center;
    justify-items: center;
    cursor: pointer;
    z-index: 3;
}
.options-container .options-item {
    color: white;
    padding: 1.5vw 4vw;
    border-radius: 1vw;
    justify-self: end;
    border: 1px solid white;
    transition: all 0.2s ease;
}
.options-item.right {
    justify-self: start;
}

.options-container .options-item.selected {
    background-color: rgba(255,255,255,0.4);
}
.options-container .options-item:hover {
    background-color: rgba(255,255,255,0.2);
}
.options-container .options-item.selected:hover {
    background-color: rgba(255,255,255,0.4);
}

@media only screen and (min-width: 800px) {
    .scoreboard-container {
        margin: -5vw 0 5vw 10vw;
        padding: 10vw 5vw 5vw;
        width: 60vw;
    }
    .scoreboard-container .scoreboard-title {
        font-size: 2vw;
        font-weight: 600;
        margin: 0;
    }
    
    .scoreboard {
        margin: 2vw 0 0;
    }
    .scoreboard-tab {
        width: 90%;
    }

    .scoreboard .scoreboard-header, .scoreboard-item{
        display: grid;
        grid-template-rows: 1fr;
        grid-template-columns: 1fr 3fr 1fr 1fr;
        font-size: 1.2vw;
        width: 90%;
    }
    .scoreboard .scoreboard-header {
        margin: 0 0 2vw 0;
        border-bottom: 1px solid #333;
        padding: 0 0 0.5vw 0;
    }
    
    .scoreboard .scoreboard-item {
        cursor: pointer;
    }
    
    .scoreboard-item {
        padding: 0.5vw 1vw;
        margin: 0.5vw -1vw;
    }
    
    .options-container {
        position: fixed;
        top: 50%;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
        bottom: unset;
        right: 0;
        left: unset;
        width: 25vw;
        height: 10vw;
        grid-template-rows: 1fr 1fr;
        grid-template-columns: 1fr;
        grid-gap: 1vw;
    }
    .options-container .options-item {
        color: white;
        padding: 0.5vw 1vw;
        border-radius: 1vw;
        align-self: end;
        justify-self: center;
        border: 1px solid white;
        width: 80%;
        text-align: center;
    }
    .options-item.right {
        align-self: start;
        justify-self: center;
    }
}
